@charset "UTF-8";

.block__subitemimage,
.block__subitemlink {
  display: none;
  color: #009EE3;
}

.block__subitemdescription {
  display: none;
}

/************* Новости*****************/

.block__content {
  display: grid;
  margin-bottom: 2rem;
}

.block__subitemdate {
  color: #999999;
}

.block__subitemdate p {
  margin: 0 0;
}

.news .block__subitem {
  opacity: 0.5;
  grid-template-columns: auto;
  grid-gap: 1rem;
}

.news .block__subitemimage {
  display: block;
}

.news .swiper-slide-active,
.news .swiper-slide-next {
  opacity: 1;
}

.news .block__content {
  grid-auto-flow: column;
  grid-gap: 0;
  grid-template-columns: 1fr;
}

.block__subitem {
  display: grid;
  align-items: center;
}

.block__subitem img {
  justify-self: center;
}

.block__subitem p {
  justify-self: center;
}

.block__subitem h4 {
  margin: 0;
}

.block__subitemcontent {
  margin-left: 1rem;
  flex: 1 50%;
}

.block__subitemtitle {
  margin-bottom: 8px;
}

.block__subitemtitle a {
  color: #373939;
  font-size: 18px;
}

.subitem.top {
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;
}

.subitem__image {
  flex: 1 auto;
}

.section_tab {
  background-color: #F9F9F9;
  background-image: url("../img/bg-tabs.png");
  padding-top: 0.1px;
  background-repeat: no-repeat;
}

.section_press {
  background-image: url("../img/bg-pressm.jpg");
  background-size: cover;
  overflow: hidden;
}

@media only screen and (min-width: 650px) {
  .block__subitemimage {
    display: block;
  }

  .block__subitem {
    display: grid;
    grid-template-columns: 174px auto;
    grid-gap: 30px;
    align-items: center;
  }
}

@media only screen and (min-width: 768px) {
  .block__subitemlink {
    display: inline-block;
  }

  .block__subitemdate {
    margin-bottom: 16px;
  }

  .block__subitemcontent {
    padding: 10px 0;
  }

  .subitem.top {
    grid-template-columns: 2fr 1fr;
  }
}

@media only screen and (min-width: 1366px) {
  .block__subitemdescription {
    display: block;
  }

  .block__subitemdescription p {
    margin: 0 0 16px;
  }

  .news .block__subitem {
    grid-template-columns: auto 1fr;
  }

  .block__subitem {
    grid-template-columns: 1fr 1fr;
  }
}

@media only screen and (max-width: 768px) {
  .newspage .block__subitem {
    border-bottom: 1px solid #999999;
  }

  .newspage .block__subitem:last-child {
    border-bottom: 1px solid #999999;
  }
}

@media only screen and (max-width: 767px) {
  .news .block__subitem:last-child {
    border-bottom: 1px solid #999999;
  }
}

@media only screen and (max-width: 650px) {
  .block__subitem {
    padding: 25px 0;
  }
}